<template>
  <div :class="{'search-open-contact':!$store.state.headerStatus}">
    <header id="wx-header">
      <!--<div class="other"><span>产品产量上报</span></div>-->
      <div class="center">
        <router-link to="/warehousing" tag="div" class="iconfont icon-return-arrow">
          <span>返回</span>
        </router-link>
        <span>产品产量审核详情</span>
      </div>
    </header>
    <div style="background:white;height:90%;padding:0 20px;">
      <van-row>
        <van-col span="24">
          <div style="padding:10px;line-height:24px;font-size:16px;">审核进度</div>
        </van-col>
      </van-row>
      <van-steps :active="active">
        <van-step>提交</van-step>
        <van-step>审核中</van-step>
        <van-step>审核结果</van-step>
      </van-steps>
      <van-row style="background:white;">
        <van-col span="24" v-if="isPass">
          <div style="text-align:center;">
            <van-button plain type="primary" size="small">审核</van-button>
          </div>
        </van-col>
      </van-row>
      <van-row>
        <van-col span="24">
          <div style="padding:10px 0;line-height:24px;font-size:16px;">产品产量详情</div>
        </van-col>
      </van-row>
      <div style="padding: 10px;border-bottom:1px solid #999;margin-bottom:10px;">
        <van-cell-group>
          <van-cell title="库区" :value="area" />
          <van-cell title="库位" :value="location" />
          <van-cell title="产品" :value="product" />
          <van-cell title="产量" :value="output" />
        </van-cell-group>
      </div>
      <van-steps direction="vertical" :active="0">
        <van-step v-for="(item,index) in logs" :key="index">
          <h3>{{item.disc}}({{item.userName}})</h3>
          <p>{{item.time}}</p>
        </van-step>
        <!-- <van-step>
          <h3>审核中</h3>
          <p>2016-07-11 10:00</p>
        </van-step>
        <van-step>
          <h3>待审核</h3>
          <p>2016-07-10 09:30</p>
        </van-step>-->
      </van-steps>
    </div>
  </div>
</template>

<script>
import { Dialog, Toast } from "vant";
export default {
  name: "detail",
  data() {
    return {
      isPass: false,
      active: null,
      active2: null,
      obj: "",
      area: "", //库区
      location: "", //库位
      product: "",
      output: "",
      id: "",
      logs: []
    };
  },
  mounted() {
    this.onLoad();
  },
  methods: {
    onLoad() {
      this.id = this.$route.query.id;
      this.journal();
    },
    journal() {
      console.log(this.id);
      this.$http
        .get("/api/warehousing/detail", { params: { id: this.id } })
        .then(res => {
          console.log(res.data.data);
          this.obj = res.data.data;
          if (this.obj.status == "通过") {
            this.active = 2;
          } else if (this.obj.status == "审核") {
            this.active = 1;
            this.isPass = true;
          } else if (this.obj.status == "不通过") {
            this.active = 2;
          }
          this.area = this.obj.area;
          this.location = this.obj.location;
          this.product = this.obj.product;
          this.output = this.obj.output;
          this.logs = res.data.data.logs.reverse();
        });
    }
  }
};
</script>

<style>
.van-field__control {
  text-align: right;
}
.van-cell {
  padding: 5px 16px;
}
</style>



